import { sprite } from '../interface/general-interfaces';
import { Player } from './player';
import { Enemy } from './enemy';
import { ViewPort } from './viewport';
import { Block } from './block';


export class DisplayController {
    
    private canvas: any;
    private ctx: CanvasRenderingContext2D;

    constructor(
         canvas: any
    ){
        this.canvas = canvas;
        this.canvas.width = 800 //window.innerWidth;
        this.canvas.height = 600 //window.innerHeight;
        this.ctx = this.canvas.getContext('2d');
    }

    initCanvas(){
     
    }

    draw(shape: string, viewPort: ViewPort, sprite: sprite): void {

        const {x, y, width, height, color } = sprite;
        this.ctx.fillStyle = color;

        switch(shape){

            case 'rectangle':
                this.ctx.fillRect(
                    x - viewPort.x,
                    y - viewPort.y,
                    width,
                    height
                );
                break;
                default:
                this.ctx.fillRect(x, y, width, height);
                break;
        }
        
    };

    drawSprite(characterImg: any, viewPort: ViewPort, sprite: sprite){

        // On va dterminer les coordonnes Y/Y du sprite  affichernsur le canvas
        let canvasX: number;
        let canvasY: number;

        if (sprite instanceof Player){
            canvasX = (this.canvas.width / 2) - sprite.width / 2;
            canvasY = (this.canvas.height - 256 - sprite.height);
        }
        if (sprite instanceof Enemy || sprite instanceof Block){
            canvasX = sprite.x - viewPort.x;
            canvasY = sprite.y - viewPort.y;
        }

        this.ctx.drawImage(
            characterImg,
            sprite.faceX, // Position X de la partie  croper
            sprite.faceY, // Position Y de la partie  croper
            32 , // Largeur de la partie  croper
            32 , // Hauteur de la partie  corper
            canvasX, // on l'affiche toujours au milieu du canvas // Position x de l'image  croper sur le canvas
            canvasY, // on l'affiche toujours au milieu du canvas // Position y de l'image  croper sur le canvas
            sprite.width, // Largeur de la partie crope
            sprite.height // Hauteur de la partie crope
        );

    };

    clear(){
        this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
    };

    drawBackground(backgroundImg: any, player: sprite){

          this.ctx.drawImage(
            backgroundImg, // Objet image reprsentant le background
            player.x - this.canvas.width / 2, // Position X de la partie  croper
            //player.y - 288.5, // Position Y de la partie  croper
            0, // Position Y de la partie  croper

            this.canvas.width , // Largeur de la partie  croper
            this.canvas.height , // Hauteur de la partie  corper
            0, // Position X sur le canvas de l'image crope
            0,  // Position Y sur le canvas de l'image crope
            this.canvas.width , // Largeur de l'image crope sur le canvas
            this.canvas.height // Hauteur de l'image crope sur le canvas
          );
    };

    drawImg(img: any, cropX: number, cropY: number, canvasX: number, canvasY: number ): void {

        this.ctx.drawImage(
            img, // Objet image reprsentant le background
            cropX, // Position X de la partie  croper
            //player.y - 288.5, // Position Y de la partie  croper
            cropY, // Position Y de la partie  croper
            64 , // Largeur de la partie  croper
            64 , // Hauteur de la partie  corper
            canvasX, // Position X sur le canvas de l'image crope
            canvasY,  // Position Y sur le canvas de l'image crope
            64 , // Largeur de l'image crope sur le canvas
            64 // Hauteur de l'image crope sur le canvas
          );
    }

    resizeCanvas(event): void {
        this.canvas.width = 800 //window.innerWidth;
        this.canvas.height = 600 // window.innerHeight;
    }
   
    drawTxt(txt: string, canvasX:number, canvasY:number): void {
        this.ctx.font = "20px Arial";
        this.ctx.fillText(txt, canvasX, canvasY);
    }
}